.main_page {

	&__header {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(30);

		@include min768 {
			margin-bottom: rem(20);
		}

		h1 {
			margin-right: rem(18);

			@include min768 {
				margin-right: rem(10);
			}
		}

		span {
			color: var(--c-text-gray);
			font-size: rem(32);

			@include min768 {
				font-size: rem(22);
			}
		}

		.back {
			width: 100%;
			margin-top: rem(16);

			@include min768 {
				display: none;
			}

			&__link {
				display: flex;
				align-items: center;

				&:hover {
					color: var(--c-main);
				}
			}

			i {
				margin-right: rem(9);
				font-size: rem(22);
			}
		}
	}

	&__wrap {
		display: flex;
		// flex-wrap: wrap;
	}

	&__aside {
		flex: 0 0 rem(200);
		margin-right: rem(40);
		border-right: rem(1) solid #C6C8DE;

		@include min970 {
			margin-right: rem(20);
		}

		@include min768 {
			display: none;
		}

		.title {
			margin-bottom: rem(15);
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			b {
				font-size: rem(18);
				margin-right: rem(7);
			}

			i {
				font-size: rem(12);
			}
		}

		.list {
			margin-left: rem(10);
			li {
				line-height: 200%;

				a {
					transition: all 200ms ease;
					color: var(--c-text-gray);

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}
	}

	&__content {
		flex: 0 0 rem(960);
		// flex: 0 0 auto;

		@include min1220 {
			flex: 1 1 auto;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			position: relative;
			width: calc((100% - 3.75rem) / 4);
			margin-right: rem(20);
			margin-bottom: rem(20);

			border-radius: rem(10);
			overflow: hidden;

			box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

			&:hover {
				.title {
					color: var(--c-main);
				}
			}

			@include min1220 {
				width: calc((100% - 2.5rem) / 3);

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@include min {
				width: calc((100% - 1.25rem) / 2);
				margin-right: rem(20) !important;

				&:nth-child(2n) {
					margin-right: 0 !important;
				}

				&:nth-child(3n) {
					// margin-right: rem(20);
				}
			}

			&:nth-child(4n) {
				margin-right: 0;

				@include min1220 {
					margin-right: rem(20);
				}

				@include min {
					margin-right: 0;
				}
			}

			&__link {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 4;
				width: 100%;
				height: 100%;
			}

			&__image {
				position: relative;
				height: rem(150);
				margin-bottom: rem(13);

				@include min480 {
					height: rem(109);
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				&-folow {
					position: absolute;
					z-index: 5;
					top: rem(10);
					right: rem(10);
					width: rem(14);
					height: rem(14);
					border-radius: 50%;
					background-color: white;

					display: flex;
					align-items: center;
					justify-content: center;

					@include min768 {
						width: rem(18);
						height: rem(18);
					}

					&:hover {
						i::before {
							color: var(--c-main);
						}
					}

					i {
						font-size: rem(8);
						margin-top: rem(1);

						@include min768 {
							font-size: rem(10);
						}

						&::before {
							color: #A1A0A0;
						}
					}
				}
			}

			&__text {
				padding: 0 rem(15) rem(22) rem(15);

				@include min480 {
					padding: 0 rem(10) rem(17) rem(10);
				}

				.title {
					font-size: rem(16);
					margin-bottom: rem(10);

					@include min480 {
						font-size: rem(14);
						margin-bottom: rem(5);
					}
				}

				p {
					color: var(--c-text-gray);
					margin-bottom: rem(10);

					@include min480 {
						font-size: rem(14);
						margin-bottom: rem(5);
					}
				}

				.price {
					font-size: rem(18);
					color: var(--c-main);
				}
			}
		}
	}

	&__map {
		border-radius: rem(10);
		overflow: hidden;

		@include min768 {
			overflow: visible;
		}

		.map_control {
			height: rem(640);

			.marker {
				&:first-child {
					left: rem(256);
					top: rem(452);
				}
				&:nth-child(3) {
					left: rem(498);
					top: rem(406);
				}
				&:nth-child(4) {
					top: rem(142);
					left: rem(590);
				}
				&:nth-child(5) {
					left: rem(610);
					top: rem(542);
				}
				&:nth-child(6) {
					left: rem(712);
					top: rem(313);
				}
				&:nth-child(7) {
					top: rem(293);
					right: rem(188);
				}
				&:nth-child(8) {
					top: rem(406);
					right: rem(90);
				}
			}

			.locale {
				position: absolute;
				top: rem(210);
				left: rem(483);

				@include min768 {
					top: rem(100);
					left: rem(121);
				}

				@include max768 {
					&:hover {
						.locale__item {
							display: flex;
						}
					}
				}

				
				i {
					position: relative;
					z-index: 11;
					font-size: rem(28);
				}
			}
		}
	}
}

.map_control {
	overflow: hidden;
	border-radius: rem(10);
	margin-bottom: rem(40);

	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

	@include min768 {
		overflow: visible;
	}


	&__iframe {
		position: relative;

		// height: rem(223);
		height: 100%;

		@include min768 {
			height: rem(144);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		.locale {
			cursor: pointer;
			&__item {
				// right: rem(10);
				// bottom: rem(10);
				position: absolute;
				width: rem(370);
				background-color: white;
				border-radius: rem(10);
				padding: rem(24);

				display: flex;
				flex-wrap: wrap;
				z-index: 10;
				box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

				opacity: 0;
				pointer-events: none;
				

				@include min768 {
					display: flex;

					width: 100%;
					top: auto !important;
					bottom: rem(-78) !important;
					right: auto !important;
					left: 0 !important;
				}

				@include min350 {
					padding: rem(14);
				}

				.item__link {
					position: absolute;
					top: 0;
					left: 0;
					z-index: 4;
					width: 100%;
					height: 100%;
				}

				&-folow {
					position: absolute;
					top: rem(27);
					right: rem(15);
					z-index: 5;

					&:hover {
						i::before {
							color: var(--c-main);
						}
					}

					i {
						font-size: rem(13) !important;
					}
				}

				&-image {
					margin-right: rem(14);

					width: rem(110);
					height: rem(92);
					border-radius: rem(10);
					overflow: hidden;

					@include min350 {
						width: rem(80);
						height: rem(62);
						margin-right: rem(7);
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				&-text {
					flex: 0 0 rem(178);
					max-width: rem(178);

					.title {
						font-size: rem(16);
						margin-bottom: rem(4);
					}

					p {
						margin-bottom: rem(8);
						color: var(--c-text-gray);
					}

					.price {
						color: var(--c-main);
						font-size: rem(18);
						font-weight: bold;
					}
				}
			}

			&__item.active {
				opacity: 1;
				pointer-events: visible;
			}
		}

		.marker {
			position: absolute;
			width: rem(20);
			height: rem(20);
			border-radius: 50%;
			background-color: var(--c-main);

			&:first-child {
				top: rem(72);
				left: rem(146);
			}
			&:nth-child(2) {
				bottom: rem(26);
				left: rem(285);
			}
			&:nth-child(3) {
				top: rem(87);
				left: rem(419);
			}
			&:nth-child(4) {
				left: rem(581);
				bottom: rem(46);
			}
			&:nth-child(5) {
				top: rem(87);
				right: rem(229);
			}
		}
	}

	&__button {
		height: rem(48);
		display: flex;
		align-items: center;
		justify-content: center;

	}
}

@include min768 {
	.smart__map {
		margin-left: -20px;
		margin-right: -20px;
		border-radius: 0;

		.map_control {
			height: rem(544);
		}
		
		.map_control__iframe {
			height: 100%;
		}
	}
}