.breadcrumbs {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(60);

	@include ipad {
		margin-bottom: rem(20);
	}

	@include min768 {
		display: none;
	}

	a {
		position: relative;
		color: var(--c-text-gray);
		margin-right: rem(34);

		&:hover {
			color: var(--c-main);
		}

		&::before {
			pointer-events: none;
			content: "/";
			position: absolute;
			top: 0;
			right: rem(-17);
			color: var(--c-text-gray);
		}
	}
	
}

.article {

	display: flex;
	flex-wrap: wrap;

	.w-full {
		display: grid;
		grid-template-columns: 0.4fr 1fr 1fr;
		grid-template-rows: auto 1fr;
		grid-template-areas: "article_preview article_preview article_header"
							 "article_preview article_preview article_info";

		@include min768 {
			// grid-template-columns: 1fr;
			grid-template-areas: 
			"article_header article_header article_header"
			"article_preview article_preview article_preview"
			"article_info article_info article_info";
		}
	}

	&__header {
		grid-area: article_header;
	}

	&__info {
		grid-area: article_info;

		@include min768 {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}

	&__full {
		@include min768 {
			margin-top: rem(20);
		}
	}

	@include min768 {
		.w-5\/12 {
			max-width: 100%;
			flex: 0 0 100%;
		}
	}

	.w-7\/12 {
		padding-right: rem(35);

		@include min {
			max-width: 100%;
			flex: 0 0 100%;

			padding-right: 0;
		}
	}

	&__slider {
		margin-bottom: rem(30);
		grid-area: article_preview;
		padding-right: rem(35);

		@include min {
			padding-right: rem(14);
		}

		.swiper-button-next,
		.swiper-button-prev {
			display: none;

			@include min768 {
				z-index: 10;
				display: flex;

				width: rem(10);
				height: rem(10);
				margin-top: rem(-5);
				background-color: white;
				border-radius: 50%;
				opacity: 1;
			}
		}

		.swiper-button-next::after {
			content: "\e90a";
			font-family: icons;
			font-size: 6px;
			color: black;
		}

		.swiper-button-prev::after {
			content: "\e90b";
			font-family: icons;
			font-size: 6px;
			color: black;
		}

		@include min768 {
			padding-right: 0;
		}

		hr {
			margin-top: rem(30);
			width: 100%;
			height: rem(1);
			background-color: #B2ABCD;

			@include min768 {display: none;}
		}
	}

	&__title {
		margin-bottom: rem(10);

		@include ipad {
			font-size: rem(22);
		}

		@include min768 {
			margin-bottom: rem(6);
		}
	}

	&__subtitle {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(20);

		@include ipad {
			margin-bottom: rem(10);
		}

		@include min768 {
			margin-bottom: rem(10);
		}

		> span {
			font-size: rem(12);
			margin-right: rem(18);
		}

		&-view {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			i {
				font-size: rem(16);
				margin-right: rem(4);
			}

			span {
				font-size: rem(12);
			}
		}
	}

	&__price {
		margin-bottom: rem(20);

		@include ipad {
			margin-bottom: rem(10);
		}

		@include min768 {
			flex: 0 0 50%;
		}

		@include min480 {
			flex: 0 0 100%;
		}

		b {
			font-size: rem(32);
		}
	}

	&__favorite {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin-bottom: rem(24);

		@include min768 {
			flex: 0 0 50%;

			justify-content: flex-end;
		}

		@include min480 {
			flex: 0 0 100%;
			justify-content: flex-start;
		}

		i,span {
			color: var(--c-text-gray);
		}

		i::before, span {
			transition: all 200ms ease;
		}

		i {
			font-size: rem(24);
			margin-right: rem(8);
		}

		span {
			text-decoration: underline;

			@include min768 {
				font-size: rem(14);
			}
			
		}
		

		&:hover {

			i::before,
			span {
				color: var(--c-main);
			}

			span {
				text-decoration: none;
			}
		}
	}

	&__button {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(26);

		@include ipad {
			margin-bottom: rem(14);
		}

		@include min660 {
			flex: 0 0 100%;
		}

		a,button {
			width: rem(283);
			height: rem(50);
			font-size: rem(18);
			display: flex;
			justify-content: center;

			@include ipad {
				height: rem(40);
				width: rem(272);
				font-size: rem(16);
			}

			@include min660 {
				width: 100%;
			}

			&:first-child {
				margin-bottom: rem(20);

				@include ipad {
					margin-bottom: rem(10);
				}
			}
		}

		.change_phone {
			font-size: rem(22);
			font-weight: bold;
			height: rem(50);
			display: none;
			justify-content: center;
			align-items: center;
			margin-bottom: rem(20);

			transition: all 200ms ease;

			&:hover {
				color: var(--c-main);
			}
		}
		.change_phone.active {
			display: flex;
		}
	}

	.company {
		width: rem(272);

		@include min768 {
			margin-left: auto;
		}

		@include min660 {
			flex: 0 0 100%;
			width: 100%;
		}
	}

	&__images {

		height: rem(432);
		margin-bottom: rem(20);
		border-radius: rem(10);
		overflow: hidden;

		@include min {
			height: rem(320);
		}

		@include min540 {
			height: rem(210);
			margin-bottom: rem(10);
		}

		.swiper-slide {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__images_thumb {
		.swiper-slide {
			cursor: pointer;
			position: relative;
			height: rem(80);
			border-radius: rem(10);
			overflow: hidden;

			

			@include min {
				height: rem(57);
			}

			&:hover {
				&::before {
					opacity: 1;
				}
			}

			&::before {
				content: "";
				position: absolute;
				border-radius: rem(10);
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				box-shadow: inset 0 0 0 3px var(--c-main);
				z-index: 3;
				transition: all 200ms ease;
				opacity: 0;
			}

			img {
				border-radius: rem(10);
				position: relative;
				z-index: 2;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.swiper-slide-thumb-active {
			&::before {
				content: "";
				position: absolute;
				border-radius: rem(10);
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				box-shadow: inset 0 0 0 3px var(--c-main);
				z-index: 3;
				transition: all 200ms ease;
				opacity: 1;
			}
		}
	}

	&__text {
		.locale {
			margin-bottom: rem(6);
		}

		.circl {
			position: relative;
			display: block;
			padding-left: rem(26);

			&::before {
				content: "";
				position: absolute;
				top: rem(4);
				left: 0;
				width: rem(11);
				height: rem(11);
				background-color: var(--c-main);
				border-radius: 50%;
			}
		}

		&-map {
			position: relative;
			margin-top: rem(20);
			margin-bottom: rem(45);
			border-radius: rem(10);
			overflow: hidden;

			height: rem(223);

			@include min540 {
				height: rem(153);
				margin-bottom: rem(20);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.locale {
				position: absolute;
				top: rem(97);
				left: rem(315);

				@include min540 {
					top: rem(62);
					left: rem(163);
				}

				i {
					font-size: rem(28);
				}
			}
		}

		ul {
			margin-top: rem(20);

			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}
				span {
					&:first-child {
						color: var(--c-text-gray);
						margin-right: rem(4);
					}
				}
			}
		}

		@include min540 {
			p {
				// font-size: rem(14);
			}
		}
	}
}


.company {
	border: rem(1) solid #B6B6B6;
	border-radius: rem(10);
	padding: rem(30);
	
	@include ipad {
		padding: rem(20);
	}

	&__title {
		margin-bottom: rem(5);
	}

	&__stats {
		margin-bottom: rem(8);

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		span {
			font-size: rem(12);

			&:first-of-type {
				margin-right: rem(6);
			}
		}

		&-list {
			display: flex;
			flex-wrap: wrap;
			margin-right: rem(6);

			i {
				font-size: rem(10);
				margin-right: rem(2);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__contact {
		margin-bottom: rem(10);

		span {
			&:first-child {
				color: var(--c-text-gray);
				margin-right: rem(4);
			}
		}
	}

	&__button {

		.button_border {
			display: flex;
			justify-content: center;
			height: rem(34);

			font-weight: normal;
			color: var(--c-text);

			&:hover {
				color: white;
			}
		}
	}
}