.user_page {
	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;

	@include min768 {
		flex-wrap: wrap;

		.user_page__smart_hide {
			display: none;
		}
	}

	&__aside {
		padding-right: rem(55);
		max-width: rem(250);
		flex: 0 0 rem(250);
		border-right: rem(1) solid #B2ABCD;

		@include min {
			padding-right: rem(20);
		}

		@include min768 {
			max-width: 100%;
			flex: 0 0 100%;
			padding-right: 0;
			border-right: 0;

			margin-bottom: rem(40);
		}
	}

	.user_page__title-smart {
		@include min768 {
			h1 {
				display: none;
			}
		}
	}

	&__content {

		flex-grow: 1;
		margin-left: rem(58);

		@include min {
			margin-left: rem(20);
		}

		@include min768 {
			flex: 0 0 100%;
			margin-left: 0;
		}

		h1 {
			margin-bottom: rem(16);

			@include min768 {
				// display: none;
			}
		}

		h2 {
			font-size: rem(32);
			margin-bottom: rem(30);

			@include min768 {
				font-size: rem(22);
				margin-bottom: rem(20);
			}
		}

		.inner-width {
			width: rem(590);

			@include min {
				width: 100%;
			}

			@include min768 {
				// width: 100%;
			}
		}

		.item_stroke,
		.item_review {
			// width: rem(590);

			margin-bottom: rem(20);

			&:last-child {
				margin-bottom: 0;
			}
		}

		.item_stroke {
			@include min768 {
				margin-bottom: rem(20) !important;
			}
		}

		.main_page__items {
			.item {
				width: rem(224);
				

				@include min768 {
					width: calc((100% - 1.25rem) / 2);
				}

				// @include min540 {
				// 	width: calc((100% - 1.25rem) / 2);
				// }
			}
		}

		&-count {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(22);

			@include min768 {
				margin-bottom: rem(14);
			}

			span {
				font-size: rem(32);
				margin-right: rem(10);

				@include min768 {
					font-size: rem(22);
				}
			}

			&_list {
				display: flex;
				flex-wrap: wrap;

				i {
					margin-right: rem(5);

					font-size: rem(25);

					@include min768 {
						margin-right: rem(3);
						font-size: rem(20);
					}
				}
			}
		}
	}
}

.user_page__settings {
	@include min768 {
		h1 {
			display: none;
		}
		.user_page__smart_hide {
			display: block;

			.user_card__image {
				width: rem(74);
				height: rem(74);
				margin-right: rem(9);
			}

			.user_card__photo_edit {
				display: flex;

				width: rem(28);
				height: rem(28);
				right: 0;
				bottom: rem(-10);

				i {
					font-size: rem(14);
				}
			}

			.user_card__nav {
				display: none;
			}

			
		}

		.flex.flex-col {
			justify-content: center;

			.user_card__title {
				margin-bottom: rem(3);
			}

			.user_card__count-list i {
				font-size: rem(12);
			}
		}
	}
}

.iframe-map {
	position: relative;
	margin-bottom: rem(30);
	height: rem(154);
	border-radius: rem(10);
	overflow: hidden;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.marker {
		width: rem(20);
		height: rem(20);
		border-radius: 50%;
		background-color: var(--c-main);
		position: absolute;
	}
}

.page_nav {
	margin-bottom: rem(20);

	ul {
		display: flex;
		flex-wrap: wrap;

		@include min768 {
			padding-bottom: rem(10);
			border-bottom: rem(1) solid #B2ABCD;
		}

		li {
			margin-right: rem(22);

			@include min768 {
				// position: relative;

				
			}

			&:last-child {
				margin-right: 0;
			}

			a {
				color: var(--c-text-gray);

				@include min768 {
					font-size: rem(16);
				}

				&:hover {
					color: var(--c-main);
					text-shadow: 0 0 .65px var(--c-main),
					0 0 .65px var(--c-main);
				}
			}

			a.active {
				color: var(--c-main);
				text-shadow: 0 0 .65px var(--c-main),
				0 0 .65px var(--c-main);

				@include min768 {
					position: relative;

					&::before {
						content: "";
						position: absolute;

						width: 100%;
						height: rem(1);
						background-color: var(--c-main);
						bottom: rem(-10.5);
						left: 0;
						z-index: 5;
					}
				}
			}
		}
		
	}
}

.user_card {
	display: flex;
	flex-direction: column;

	@include min768 {
		flex-wrap: wrap;
		flex-direction: row;
	}

	&__photo_edit {
		position: absolute;
		z-index: 5;
		bottom: rem(2);
		right: rem(17);

		width: rem(20);
		height: rem(20);
		border-radius: 50%;
		background-color: white;

		display: flex;
		align-items: center;
		justify-content: center;

		@include min768 {
			display: none;
		}

		&:hover {

			i::before {
				color: var(--c-main);
			}
		}

		i {
			font-size: rem(10);
		}
	}

	&__image {
		position: relative;
		border-radius: 50%;
		width: rem(122);
		height: rem(122);
		// overflow: hidden;

		margin-bottom: rem(12);

		@include min768 {
			width: rem(93);
			height: rem(93);
			margin-bottom: 0;
			margin-right: rem(23);
		}

		img {
			border-radius: 50%;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		margin-bottom: rem(10);

		@include min768 {
			margin-bottom: rem(8);
			// flex: 1 1 auto;
		}

		h2 {
			// font-size: rem(24);
			@include min768 {
				font-size: rem(22);
			}
		}
	}

	&__count {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(16);

		@include min768 {
			margin-bottom: rem(7);
		}

		span {
			&:first-child {
				margin-right: rem(7);

				@include min768 {
					margin-right: rem(5);
				}
			}
		}

		&-list {
			display: flex;
			flex-wrap: wrap;
			margin-right: rem(8);

			i {
				font-size: rem(12);
				margin-right: rem(2);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__text {
		margin-bottom: rem(20);

		@include min768 {
			margin-bottom: rem(8);
		}
	}

	&__nav {
		margin-top: rem(12);
		ul {
			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					transition: all 200ms ease;
					&:hover {
						color: var(--c-main);
					}
				}

				a.active {
					font-weight: bold;

					&:hover {
						color: var(--c-text);
					}
				}
			}
		}
	}

	.button_border {
		align-self: flex-start;

		@include min768 {
			height: rem(34);
		}
	}
}