.profile_mobile {
	display: none;

	@include min768 {
		position: fixed;
		z-index: 90;
		top: 0;
		left: 0;
		// width: calc(100% - 40px);
		background-color: white;
		height: 100%;
		width: 100%;

		overflow-y: auto;

		display: block;

		transform: translateX(-100%);
		transition: transform 300ms ease;

		padding-top: rem(68);

		

		&__header {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			margin-bottom: rem(20);

			img {
				width: rem(74);
				height: rem(74);
				border-radius: 50%;
				margin-right: rem(9);
			}

			.text {
				display: flex;
				flex-direction: column;

				h3 {
					margin-bottom: rem(6);
					font-size: rem(22);
				}

				.count {
					display: flex;
					flex-wrap: wrap;
					align-items: center;

					span {
						&:first-child {
							margin-right: rem(7);
						}
					}

					&__list {
						margin-right: rem(8);
						display: flex;
						flex-wrap: wrap;
						align-items: center;

						i {
							font-size: rem(12);

							margin-right: rem(3);

							&:last-child {
								margin-right: 0;
							}
						}
					}
				}
			}
		}

		&__button {
			margin-bottom: rem(20);

			.button_orange {
				display: flex;
				justify-content: center;
			}
		}

		&__nav {
			margin-bottom: rem(55);
			ul {

				li {

					margin-bottom: rem(20);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(22);
					}
				}
			}
		}
	}
}

.profile_mobile.active {
	transform: translateX(0%);
}