.header {
	height: rem(76);
	

	background-color: #FEFEFE;
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, .06);

	@include min768 {
		// position: sticky;
		height: rem(46);
		// top: 0;
		z-index: 91;
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		height: 100%;

		
	}

	&__left {
		.burger {
			display: none;
		}

		.burger.burger_active {
			@include min768 {
				span {
					opacity: 0;
				}

				&::before {
					transform: rotate(45deg) translate(rem(4), rem(4));
				}

				&::after {
					transform: rotate(-45deg) translate(rem(4), rem(-4));
				}
			}
		}

		@include min768 {
			

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.logo {
				max-width: rem(87);
				margin-top: rem(3);
			}

			.burger {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				width: rem(18);
				height: rem(14);

				margin-right: rem(7);

				&::before,
				&::after {
					content: "";
					width: 100%;
					height: rem(2);
					border-radius: rem(8);
					background-color: var(--c-text);

					transition: all 200ms ease;
				}

				span {
					width: 100%;
					border-radius: rem(8);
					background-color: var(--c-text);
					height: rem(2);

					transition: all 200ms ease;
					
				}
			}
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;

		&-button {
			margin-right: rem(40);

			@include min970 {
				margin-right: rem(24);
			}

			@include min768 {
				margin-right: rem(14);
			}

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.item {
				position: relative;
				margin-right: rem(24);

				@include min970 {
					margin-right: rem(14);
					&:nth-child(2) {
						display: none;
					}
				}
				

				span {
					position: absolute;
					top: rem(-4);
					right: rem(-6);
					font-size: rem(8);
					color: white;
					background-color: var(--c-main);
					width: rem(12);
					height: rem(12);
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
				}

				&:last-child {
					margin-right: 0;
				}

				&:hover {
					i::before {
						color: var(--c-main);
					}
				}
			}

			.icon-mail {
				font-size: rem(28);
			}

			i {
				font-size: rem(24);

				&::before {
					transition: all 200ms ease;
				}
			}
		}

		&-user {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.first {
				margin-right: rem(28);

				@include min970 {
					// display: none;
					margin-right: rem(14);
				}

				@include min768 {
					display: none;
				}

				&:hover {
					color: var(--c-main);
				}
			}

			.user_panel {
				margin-right: rem(24);

				@include min970 {
					margin-right: rem(14);
				}
				
				@include min768 {
					margin-right: 0;
				}
			}

			@include min768 {
				.button_orange {
					display: none;
				}
			}
		}
	}
}

.user_panel {
	position: relative;
	
	&__drop {
		position: absolute;
		top: rem(60);
		left: rem(-24);
		width: rem(242);
		border-radius: rem(10);
		padding: rem(20) 0;
		background-color: white;
		box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
		z-index: 100;
		display: none;

		@include min768 {
			display: none !important;
		}

		.count {
			padding: 0 rem(20) rem(10) rem(20);
			margin-bottom: rem(20);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			border-bottom: rem(1) solid #B2ABCD;

			span {
				&:first-child {
					margin-right: rem(7);
				}
			}
			
			&__list {
				margin-right: rem(8);
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				i {
					font-size: rem(12);

					margin-right: rem(3);

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

		ul {
			padding: 0 rem(20);

			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}
				a {

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}
	}

	&__drop.active_drop {
		display: block;
	}
	

	&__button {
		cursor: pointer;
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&:hover {
			span,
			.icon-arrow-bottom::before {
				color: var(--c-main);
			}
		}

		.icon-profile-user {
			pointer-events: none;
			font-size: rem(26);
			margin-right: rem(6);

			@include min768 {
				margin-right: 0;
			}
		}
		span {
			pointer-events: none;
			margin-right: rem(7);

			@include min970 {
				display: none;
			}
		}
		.icon-arrow-bottom {
			pointer-events: none;
			font-size: rem(8);

			@include min768 {
				display: none;
			}
		}
	}

	&__button.active {
		span,
		.icon-arrow-bottom::before {
			color: var(--c-main);
		}
	}
}

.header.header_fixed {
	@include min768 {
		position: fixed;
		top: 0;
		width: 100%;
	}
}

.header_scroll {
	&::before {
		content: "";
		height: rem(46);
		display: block;
	}
}