.item_stroke {
	position: relative;
	display: flex;
	// flex-wrap: wrap;

	border-radius: rem(10);
	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

	overflow: hidden;

	&:hover {
		.title {
			color: var(--c-main);
		}
	}

	@include min768 {
		width: calc((100% - 1.25rem) / 2);

		margin-right: rem(20);

		&:nth-child(2n) {
			margin-right: 0;
		}
	}

	@include min660 {
		width: 100%;
		margin-right: 0 !important;
	}

	&__link {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 4;
		width: 100%;
		height: 100%;
	}

	&__image {
		max-width: rem(224);
		flex: 0 0 rem(224);
		height: 100%;

		margin-right: rem(24);

		@include min {
			max-width: rem(154);
			flex: 0 0 rem(154);
		}

		@include min768 {
			max-width: rem(108);
			flex: 0 0 rem(108);
			margin-right: rem(12);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		padding: rem(27) rem(57) rem(27) 0;

		@include min768 {
			padding: rem(17) rem(36) rem(17) 0;
		}
		
		.title {
			margin-bottom: rem(10);
			font-weight: bold;

			

			@include min768 {
				margin-bottom: rem(7);
				font-size: rem(14);
			}
		}

		p {
			color: var(--c-text-gray);
			margin-bottom: rem(10);

			@include min768 {
				margin-bottom: rem(7);
			}
		}

		.price {
			font-size: rem(18);
			color: var(--c-main);
			font-weight: bold;
		}
	}

	&__control {
		top: rem(10);
		right: rem(12);
		position: absolute;
		z-index: 5;
		

		display: flex;
		flex-wrap: wrap;

		.control__smart {
			display: none;

			@include min768 {
				display: block;

				&:hover {
					~.control__smart_wrap {
						display: block;
					}
				}
			}
		}

		.control__smart_wrap {
			@include min768 {
				position: absolute;
				top: rem(24);
				right: rem(12);
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				padding: rem(12) rem(18);
				border-radius: rem(10);
				border-top-right-radius: 0;

				border: rem(1) solid #D2D2D2;
				background-color: white;

				z-index: 5;

				display: none;
			}
		}

		button {
			margin-right: rem(9);

			@include min768 {
				margin-right: 0;
			}

			span {
				display: none;

				@include min768 {
					display: block;
					margin-bottom: rem(4);
					font-size: rem(12);
				}
			}

			&:hover {
				i::before {
					color: var(--c-main);
				}
			}

			i {
				font-size: rem(19);

				@include min768 {
					display: none;
				}
			}

			.icon-b-open {
				@include min768 {
					display: block;
				}
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.item_review {
	width: 100%;
	display: flex;
	flex-direction: column;
	padding: rem(20) rem(25);
	background-color: white;
	border-radius: rem(10);
	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

	@include min768 {
		padding: rem(14);
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(8);

		img {
			margin-right: rem(8);
			border-radius: 50%;
			width: rem(31);
			height: rem(31);
		}

		.title {
			font-weight: bold;
		}
	}

	&__text {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(14);

		.count {
			margin-right: rem(8);
			display: flex;
			flex-wrap: wrap;

			i {
				margin-right: rem(2);
				font-size: rem(12);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		p {
			color: var(--c-text-gray);
		}
	}

	p {

	}

}

.wrap_fixed {
	display: flex;
	flex-direction: column;
}

.people_item {
	position: relative;
	background-color: white;
	border-radius: rem(10);
	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
	padding: rem(22) rem(25);
	display: flex;
	flex-direction: column;
	align-self: flex-start;

	margin-bottom: rem(20);

	@include min768 {
		box-shadow: none;
		padding: 0;
		border-radius: 0;

		border-bottom: rem(1) solid #B2ABCD;
	}

	&:last-child {
		margin-bottom: 0;
	}
	
	.button_border {
		display: flex;
		align-self: flex-start;
		height: rem(34);
		font-size: rem(16);
		font-weight: normal;

		@include min768 {
			position: absolute;
			top: rem(60);
			left: rem(115);
		}

		@include min350 {
			left: rem(105);
		}
	}

	&__title {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(15);

		img {
			margin-right: rem(8);
			width: rem(48);
			height: rem(48);
			border-radius: 50%;

			@include min768 {
				width: rem(93);
				height: rem(93);
				margin-right: rem(22);
			}

			@include min350 {
				margin-right: rem(10);
			}
		}

		.text {
			display: flex;
			flex-direction: column;

			@include min768 {
				margin-bottom: rem(40);
			}

			.title {
				font-weight: bold;
				margin-bottom: rem(5);

				@include min768 {
					margin-bottom: rem(6);
					font-size: rem(22);
				}
			}

			span {
				color: var(--c-text-gray);
			}
		}
	}

	.item {
		box-shadow: none;

		@include min768 {
			box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
		}

		&:last-child {
			margin-right: 0;
		}

		&__image {
			margin-bottom: 0;
		}

		&__text {
			padding-top: rem(12);
			border-bottom: rem(1) solid #B2ABCD;
			border-left: rem(1) solid #B2ABCD;
			border-right: rem(1) solid #B2ABCD;

			border-bottom-left-radius: rem(10);
			border-bottom-right-radius: rem(10);

			@include min768 {
				border-bottom: 0;
				border-left: 0;
				border-right: 0;
			}
		}
	}
}

.message_items {
	display: flex;
	flex-direction: column;

	.item {
		position: relative;
		border-radius: rem(10);
		box-shadow: 0px 4px 5px 0px rgba(0, 0, 0,.08);
		padding: rem(18) rem(25);
		background-color: white;

		margin-bottom: rem(20);

		display: flex;
		flex-wrap: wrap;

		@include min768 {
			margin-bottom: rem(10);
			padding: rem(15) rem(20);
		}

		&:last-child {
			margin-bottom: 0;
		}

		&__info {
			cursor: pointer;
			position: absolute;
			top: rem(9);
			right: rem(14);
			

			&:hover {
				i::before {
					color: #434343;
				}

				ul {
					display: flex;
				}
			}

			i {
				font-size: rem(14);
				color: #C4C4C4;
			}

			ul {
				position: absolute;
				top: rem(17);
				right: rem(6);

				border-radius: rem(10);
				border-top-right-radius: 0;

				background-color: white;
				border: rem(1) solid #D2D2D2;
				box-shadow: 0px 4px 5px 0px rgba(0, 0, 0,.08);
				padding: rem(12) rem(20);

				// display: flex;
				flex-direction: column;
				align-items: flex-start;


				display: none;

				li {
					margin-bottom: rem(5);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						white-space: nowrap;

						&:hover {
							color: var(--c-main);
						}
					}
				}
			}
		}

		&__left {
			margin-right: rem(9);

			img {
				width: rem(48);
				height: rem(48);
				border-radius: 50%;
			}
		}

		&__right {
			display: flex;
			flex-direction: column;

			&-header {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(5);

				@include min768 {
					flex-direction: column;
					align-items: flex-start;
				}

				.title {
					margin-right: rem(5);
					font-weight: bold;

					@include min768 {
						font-size: rem(16);
						display: block;
					}
				}

				span {
					color: var(--c-text-gray);

					@include min768 {
						font-size: rem(10);
						display: block;
					}
				}
			}

			p {
				
			}
		}
	}
}

.message_main {
	border-radius: rem(10);
	background-color: white;
	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0,.08);
	max-width: rem(793);

	@include min1220 {
		max-width: 100%;
		width: 100%;
	}

	// @include min768 {
	// 	max-width: 100%;
	// 	width: 100%;
	// }

	&__header {
		margin: 0 rem(2);
		padding: rem(15) rem(22) rem(15) rem(25);

		border-bottom: rem(1) solid #B2ABCD;

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.link_back {
			margin-right: rem(6);

			@include min768 {
				margin-right: rem(10);
			}

			&:hover {
				i::before {
					color: var(--c-main);
				}
			}

			i {
				font-size: rem(20);
			}
		}

		.user {
			display: flex;
			flex-wrap: wrap;

			&__image {
				width: rem(44);
				height: rem(44);
				border-radius: 50%;
				overflow: hidden;
				margin-right: rem(13);

				@include min768 {
					width: rem(31);
					height: rem(31);
					margin-right: rem(8);
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			&__text {
				display: flex;
				flex-direction: column;

				.title {
					font-weight: bold;
					margin-bottom: rem(5);

					@include min768 {
						font-size: rem(16);
						margin-bottom: rem(2);
					}
				}

				p {
					color: var(--c-text-gray);

					@include min768 {
						font-size: rem(10);
					}
				}
			}
		}

		.user__control {
			position: relative;
			cursor: pointer;
			margin-left: auto;

			i {
				font-size: rem(14);
				color: #C4C4C4;
			}

			&:hover {
				i::before {
					color: #434343;
				}

				ul {
					display: flex;
				}
			}

			ul {
				position: absolute;
				top: rem(17);
				right: rem(6);
				z-index: 10;

				border-radius: rem(10);
				border-top-right-radius: 0;

				background-color: white;
				border: rem(1) solid #D2D2D2;
				box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
				padding: rem(12) rem(20);

				// display: flex;
				flex-direction: column;
				align-items: flex-start;


				display: none;

				li {
					margin-bottom: rem(5);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						white-space: nowrap;

						&:hover {
							color: var(--c-main);
						}
					}
				}
			}
		}
	}

	&__canvas {
		display: flex;
		flex-direction: column;
		height: rem(300);
		justify-content: flex-end;

		padding: rem(20) rem(25);

		margin: 0 rem(2);
		border-bottom: rem(1) solid #B2ABCD;

		@include min768 {
			padding: rem(20) 0;
			margin: 0;
		}

		.item {
			position: relative;
			width: rem(364);
			border-radius: rem(25);
			background-color: #F2F7FE;
			padding: rem(14) rem(50) rem(14) rem(28);

			margin-bottom: rem(20);

			@include min768 {
				width: rem(274);
			}

			@include min350 {
				width: rem(244);
				padding: rem(7) rem(45) rem(7) rem(14);
			}

			&:last-child {
				margin-bottom: 0;
			}

			span {
				position: absolute;
				bottom: rem(6);
				right: rem(20);
				font-size: rem(12);
				color: var(--c-text-gray);
			}
		}

		.item_left {
			align-self: flex-start;
		}
		.item_right {
			align-self: flex-end;
		}
	}


	&__add {
		padding: rem(20) rem(25) rem(27) rem(25);

		@include min768 {
			padding: rem(20) 0 rem(20) 0;
		}

		.field-set {

			.field {
				width: 100%;
				padding: rem(16) rem(120) rem(16) rem(22);
				height: rem(52);

				// max-height: rem(120);

				overflow: hidden;
				resize: none;

				@include min350 {
					font-size: rem(12);
				}
			}

			&__control {
				cursor: pointer;
				position: absolute;
				top: rem(17);
				right: rem(18);

				display: flex;
				flex-wrap: wrap;

				.item {
					input {
						display: none;
					}
					cursor: pointer;
					margin-right: rem(14);

					@include min768 {
						margin-right: rem(9);
					}

					&:hover {
						i::before {
							color: var(--c-main);
						}
					}

					&:last-child {
						margin-right: 0;
					}

					i {
						font-size: rem(17);
					}
				}
			}
		}
	}
}

.user_settings {
	width: rem(488);

	@include min768 {
		width: 100%;
	}

	.form {
		margin-bottom: rem(60);

		@include min768 {
			margin-bottom: rem(40);
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.field-set {
		margin-bottom: rem(30);

		@include min768 {
			margin-bottom: rem(20);
		}

		.field,
		.select-selected {
			width: 100%;
			height: rem(52);

			color: var(--c-text);
		}

		.select-selected {
			&::after {
				content: "\e901";
				top: rem(22);
				right: rem(25);
				color: #C4C4C4;
				font-size: rem(12);
			}
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.button_orange {
		height: rem(52);
		font-size: rem(18);

		@include min540 {
			width: 100%;
			display: flex;
			justify-content: center;
		}
	}
}