footer.footer {

	// position: absolute;
	// bottom: 0;
	// width: 100%;

	
	background-color: #FEFEFE;
	margin-top: auto;

	padding: rem(16) 0;

	.container {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;

		@include min768 {
			flex-direction: column;
		}
	}

	p {
		font-size: rem(12);
		color: var(--c-text-gray);
		margin-right: rem(64);

		@include min768 {
			margin-right: 0;
			margin-bottom: rem(8);
		}
	}

	a {
		font-size: rem(12);
		color: var(--c-text-gray);

		&:hover {
			text-decoration: underline;
		}
	}
}