

.user_wrap {
	position: relative;
	width: rem(710);

	border-radius: rem(10);
	background-color: white;

	padding: rem(40) rem(60);

	@include min768 {
		width: 100%;
		padding: rem(35) rem(20);

		margin-left: 20px;
		margin-right: 20px;
	}

	&-title {
		margin-bottom: rem(7);

		h3 {
			font-size: rem(32);
			font-weight: bold;

			@include min768 {
				font-size: rem(22);
			}

			@include min350 {
				font-size: rem(18);
			}
		}
	}

	&-review {
		margin-bottom: rem(35);

		@include min768 {
			margin-bottom: rem(20);
		}

		.button_orange {
			font-size: rem(18);
			height: rem(52);

			@include min768 {
				width: 100%;
				display: flex;
				justify-content: center;
			}
		}

		&_count {
			// margin-bottom: rem(14);

			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(22);

			@include min768 {
				margin-bottom: rem(14);
			}

			span {
				font-size: rem(32);
				margin-right: rem(10);

				@include min768 {
					font-size: rem(22);
					margin-right: rem(6);
				}
			}

			.user_wrap-reviewt_list {
				display: flex;
				flex-wrap: wrap;

				i {
					margin-right: rem(5);

					font-size: rem(25);

					@include min768 {
						font-size: rem(20);
						margin-right: rem(4);
					}
				}
			}
		}
	}

	.item_review {
		// box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
		margin-bottom: rem(20);

		&:last-child {
			margin-bottom: 0;
		}

	}

	&-field {
		margin-bottom: rem(24);

		.field {
			width: 100%;
			height: rem(52);
		}

		textarea.field {
			height: rem(123);
			resize: none;
		}
	}

	&-button {
		display: flex;
		flex-wrap: wrap;

		@include min540 {
			justify-content: space-between;
		}

		button {
			font-size: rem(18);
			height: rem(52);

			margin-right: rem(20);

			@include min540 {
				// flex: 1 1 auto;
				margin-right: rem(14);
				padding: 0 rem(20);
			}

			@include min340 {
				width: 100%;
				padding: 0 rem(30);
				margin-right: 0;
				display: flex;
				justify-content: center;

				&:first-child {
					margin-bottom: rem(10);
				}
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&-article {
		display: flex;

		height: rem(75);
		margin-bottom: rem(15);

		border-radius: rem(10);
		overflow: hidden;

		box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

		&:hover {
			.text b {
				color: var(--c-main);
			}
		}


		&:last-child {
			margin-bottom: 0;
		}
		
		.image {
			flex: 0 0 rem(87);
			max-width: rem(87);

			margin-right: rem(20);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.text {
			display: flex;
			flex-direction: column;
			justify-content: center;

			b {
				margin-bottom: rem(3);
				font-weight: bold;
			}

			span {
				font-weight: bold;
				font-size: rem(18);
				color: var(--c-main);
			}
		}
	}

	&-rating {
		width: rem(230);
		display: flex;
		flex-wrap: wrap;
		flex-direction: row-reverse;
		justify-content: flex-end;
		overflow: hidden;

		margin-bottom: rem(20);

		&:not(:checked) > input {
			display: none;
		}

		label {
			margin-right: rem(7);

			&:first-child {
				margin-right: 0;
			}
		}

		input {
			&:checked ~ label {
				i {
					&::before {
						color: var(--c-main);
					}
				}
			}
		}

		&:not(:checked)>label {
			float: right;
			cursor: pointer;
			
			i {
				font-size: rem(37);

				@include min768 {
					font-size: rem(20);
				}
				
				&::before {
					color: var(--c-text-gray);
				}
			}
		}

		&:not(:checked)>label:hover,
		&:not(:checked)>label:hover ~ label {
			i {
				&::before {
					color: var(--c-main);
				}
			}
		}

		input:checked + label:hover,
		input:checked+label:hover ~ label,
		input:checked~label:hover,
		input:checked~label:hover ~ label,
		input:hover~input:checked ~ label {
			i {
				&::before {
					color: var(--c-main);
				}
			}
		}
	}
}

.user_wrap_small {
	width: rem(608);

	.user_wrap-title {
		margin-bottom: rem(20);
	}
}