.window_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.6);
	z-index: 200;

	// display: flex;
	align-items: center;
	justify-content: center;

	overflow-y: auto;

	display: none;
}

.window_overlay.active {
	display: flex;
}

.window-close {
	position: absolute;
	top: rem(10);
	right: rem(10);

	&:hover {
		i::before {
			color: var(--c-main);
		}
	}

	i {
		font-size: rem(15);
	}
}

.auth_wrap {
	position: relative;
	width: rem(480);
	border-radius: rem(10);
	background-color: white;

	padding-bottom: rem(45);


	@include min768 {
		width: 100%;

		// padding: rem(35) rem(20);
		padding-bottom: rem(32);
		margin: 0 rem(20);
	}
	

	&-title {
		margin-top: rem(40);
		margin-bottom: rem(20);

		
		margin-left: auto;
		margin-right: auto;

		@include min768 {
			margin-top: rem(35);
			
		}

		h3 {
			max-width: rem(287);
			text-align: center;
			font-size: rem(32);

			margin-left: auto;
			margin-right: auto;

			@include min768 {
				font-size: rem(22);
				// max-width: rem(200);
				// text-align: center;
			}
		}

		p {
			margin-top: rem(14);
			text-align: center;
			margin-left: auto;
			margin-right: auto;
			padding: 0 rem(60);

			@include min768 {
				padding: 0 rem(40);
			}
		}
	}

	&-content {
		padding: 0 rem(60);

		@include min768 {
			padding: 0 rem(20);
		}

		.field-button {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;

			padding-bottom: rem(55);

			@include min768 {
				padding-bottom: 0;
			}

			.button_orange, .button_border {
				flex-wrap: 1;
				width: auto;

				@include min768 {
					width: 100%;
				}
			}

			.button_orange {
				@include min768 {
					margin-bottom: rem(20);
				}
			}
		}

		.field-set {
			// width: 100%;
			margin-bottom: rem(40);

			&:last-of-type {
				margin-bottom: rem(20);
			}

			.field {
				width: 100%;
				height: rem(52);

				@include min768 {
					height: rem(40);
				}
			}

			a {
				// margin-top: rem(8);
				font-size: rem(12);
				display: flex;
				align-items: center;


				&:hover {
					color: var(--c-main);
				}
			}

			.code {
				margin-top: rem(8);
			}

			.field_radio {
				cursor: pointer;

				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: 0;

				input {
					display: none;

					&:checked ~ .check {
						&::before {
							opacity: 1;
						}
					}
				}

				.check {
					width: rem(20);
					height: rem(20);
					border-radius: rem(3);
					border: rem(1) solid #B6B6B6;

					display: flex;
					align-items: center;
					justify-content: center;

					margin-right: rem(6);

					&::before {
						content: "";
						width: rem(12);
						height: rem(12);
						border-radius: rem(3);
						background-color: var(--c-main);
						opacity: 0;
						transition: all 200ms ease;
					}
				}
				
				.text {
					font-weight: normal;

					@include min768 {
						font-size: rem(14);
					}
				}
			}
		}

		.field-last {
			margin-bottom: rem(20);
		}

		.field-between {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			margin-bottom: rem(20);

			a {
				color: var(--c-text-gray);

				&:hover {
					color: var(--c-main);
				}
			}
		}

		.button_orange,
		.button_border {
			height: rem(50);
			width: 100%;
			display: flex;
			justify-content: center;
			font-size: rem(18);
			padding: 0 rem(40);
		}
	}

	&-form {

		&__order {
			margin-top: rem(27);

			display: flex;
			flex-direction: column;
			align-items: center;

			span {
				margin-bottom: rem(16);
			}
		}

		&__social {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(34);

			a {
				margin-right: rem(17);
				transition: all 200ms ease;

				&:hover {
					opacity: .8;
				}

				&:last-child {
					margin-right: 0;
				}
			}
		}

		&__bottom {
			border-top: rem(1) solid #B2ABCD;
			padding-top: rem(24);
			// padding-bottom: rem(45);

			display: flex;
			flex-direction: column;
			align-items: center;

			.link {
				color: #777494;
				margin-bottom: rem(20);
			}

			p {
				max-width: rem(270);
				font-size: rem(12);
				text-align: center;

				a {
					display: block;
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}
		}
	}
}