.wrap_user {
	margin-top: rem(60);
	margin-bottom: rem(90);

	@include min768 {
		margin-top: rem(40);
		margin-bottom: rem(40);
	}
}

.form_add {
	@include min768 {
		.w-9\/12 {
			max-width: 100%;
			flex: 0 0 100%;
		}
	}
	&__title {
		margin-bottom: rem(30);

		@include min768 {
			margin-bottom: rem(20);
		}
	}

	&__wrap {
		display: flex;
		flex-direction: column;

		.select-selected {
			padding: 0 rem(26);
			&::after {
				content: "\e901";
				color: #C4C4C4;
			}
		}

		.select-items div, .field {
			padding: 0 rem(26);
		}

		textarea.field {
			padding: rem(17) rem(26);
		}

		.w-50 {
			width: rem(387) !important;

			@include min768 {
				width: 100% !important;
			}
		}

		.field-price {
			position: relative;

			.field {
				padding-right: rem(50);
			}

			.price {
				position: absolute;
				top: rem(12);
				right: rem(16);
			}
		}

		.field-button {
			@include min540 {
				.button_orange {
					width: 100%;
					display: flex;
					justify-content: center;
				}
			}
		}

		.field-set {
			width: 100%;
			margin-bottom: rem(40);

			@include min768 {
				margin-bottom: rem(20);
			}

			&__group {
				display: flex;
				flex-wrap: wrap;

				.field {
					width: calc((100% - 1.25rem) / 2) !important;

					margin-right: rem(20);

					&:last-child {
						margin-right: 0;
					}
				}
			}

			&__map {
				position: relative;
				height: rem(154);
				border-radius: rem(10);
				overflow: hidden;

				margin-top: rem(20);

				box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, .25);
				

				.iframe {
					width: 100%;
					height: 100%;

					.marker {
						position: absolute;
						top: rem(62);
						left: rem(362);

						@include min540 {
							top: rem(40);
							left: rem(149);
						}

						i {
							font-size: rem(28);
						}
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}

			&__checked {
				display: flex;
				margin-top: rem(26);

				@include min768 {
					margin-top: rem(20);
				}
				
				.field-checked {
					max-width: 50%;
					flex: 0 0 50%;

					display: flex;

					cursor: pointer;

					@include min768 {
						max-width: auto;
						flex: 0 0 auto;

						margin-right: rem(16);

						&:last-child {
							margin-right: 0;
						}

						.text {
							font-size: rem(16);
						}
					}
				}

				// flex-wrap: wrap;

				input {
					display: none;

					&:checked ~ .check::before {
						opacity: 1;
					}
				}

				.check {
					width: rem(19);
					height: rem(19);
					flex: 0 0 rem(19);
					border-radius: 50%;
					border: rem(1) solid #B6B6B6;

					margin-right: rem(10);

					position: relative;

					&::before {
						content: "";
						position: absolute;
						top: rem(3);
						left: rem(3);
						transition: all 200ms ease;
						width: rem(11);
						height: rem(11);
						border-radius: 50%;
						background-color: var(--c-main);
						opacity: 0;
					}
				}

				.message {
					margin-left: rem(5);
				}
			}

			.image_items {
				display: flex;
				flex-wrap: wrap;
				margin-right: rem(20);

				@include min480 {
					width: calc((100% - 1.25rem) / 2);
				}

				.item {
					position: relative;
					width: rem(183);
					height: rem(142);
					border-radius: rem(10);
					overflow: hidden;

					margin-right: rem(20);

					@include min480 {
						height: rem(117);
					}

					&:last-child {
						margin-right: 0;
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					&__delete {
						position: absolute;
						top: rem(10);
						right: rem(10);

						background-color: white;
						border-radius: 50%;

						width: rem(20);
						height: rem(20);

						display: flex;
						align-items: center;
						justify-content: center;

						@include min480 {
							width: rem(24);
							height: rem(24);
						}

						&:hover {
							// background-color: var(--c-main);

							i {
								margin-left: rem(1);
								&::before {
									color: var(--c-main);
								}
							}
						}
					}
				}
			}

			.field-upload {
				cursor: pointer;
				width: rem(183);
				height: rem(142);
				border-radius: rem(10);
				border: rem(1) solid #B6B6B6;

				display: flex;
				align-items: center;
				justify-content: center;

				@include min480 {
					width: calc((100% - 1.25rem) / 2);
					height: rem(117);
				}

				&:hover {
					i::before {
						color: var(--c-main);
					}
				}

				input {
					display: none;
				}

				i {
					font-size: rem(30);
				}
			}

			.field {
				width: 100%;
			}
		}
	}
}

