.filter_form {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(46);

	@include min768 {
		margin-bottom: rem(20);

		.smart-filter {
			display: none !important;
		}
	}

	&__smart {
		display: none;

		@include min768 {
			display: block;
			margin-top: rem(20);
			// margin-bottom: rem(20);

			order: 9;

			.title {
				margin-bottom: rem(7);
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				b {
					font-size: rem(14);
					margin-right: rem(11);
				}

				i {
					font-size: rem(12);
				}
			}

			.list {
				margin-left: rem(10);

				li {
					line-height: 200%;

					a {
						transition: all 200ms ease;
						color: var(--c-text-gray);
						font-size: rem(14);

						&:hover {
							color: var(--c-main);
						}
					}
				}
			}
		}
	}

	&__fields {
		display: flex;
		margin-bottom: rem(16);

		@include min768 {
			margin-bottom: 0;
			flex-wrap: wrap;
		}
		
		.custom-select {
			max-width: rem(180);
			flex: 0 0 rem(180);
			background-color: var(--c-main-bg);

			@include min768 {
				display: none;
				background-color: white;
			}
		}

		.select-selected {
			position: relative;
			background-color: var(--c-main-bg);
		}

		.first {
			
			.select-selected {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-right: 0;
				
			}
		}

		.last {
			.select-selected {
				border-radius: 0;
				// border-right: 0;

				@include min768 {
					height: rem(50);
					background-color: white;
					border-radius: rem(6);

					&::after {
						content: "\e901";
						font-size: rem(12);
						color: #C4C4C4;
						top: rem(20);
					}
				}
			}

			label {
				display: none;
			}

			@include min768 {
				display: block;
				max-width: 100%;
				flex: 0 0 100%;

				order: 10;

				margin-top: rem(20);

				label {
					font-weight: bold;
					font-size: rem(14);
					margin-bottom: rem(10);
					display: block;
				}
			}
		}

		.filter-input {
			.field {
				border-left: 0;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
				background-color: var(--c-main-bg);

				@include min768 {
					border-left: rem(1) solid #9A9EBC;
					border-top-left-radius: rem(5);
					border-bottom-left-radius: rem(5);
					background-color: white;
				}
			}
			.filter_setting_active {
				display: none;

				@include min768 {
					position: absolute;
					top: rem(10);
					right: rem(12);
					display: block;

					i {
						font-size: rem(20);
						pointer-events: none;
					}
				}
			}
			.filter_setting_active.open_filter {
				i {
					&::before {
						color: var(--c-main);
					}
				}
			}
		}

		.filter-input {
			position: relative;
			flex-grow: 1;

			@include min768 {
				width: 100%;
				flex: 0 0 100%;
			}

			.field {
				width: 98%;
				padding-right: rem(100);
				border-color: #9A9EBC;

				@include min768 {
					width: 100%;
					padding-right: rem(35);
				}
			}

			.button_orange {
				position: absolute;
				top: 0;
				right: 0;

				@include min768 {
					display: none;
				}
			}
		}
	}

	&__checked {
		display: flex;
		flex-wrap: wrap;

		@include min768 {
			// display: none;
			margin-top: rem(20);
			flex-direction: column;
		}

		.field-checked {
			cursor: pointer;
			display: flex;
			align-items: center;

			margin-right: rem(12);

			@include min768 {
				margin-right: 0;
				margin-bottom: rem(11);

				span {
					font-size: rem(14);
				}
			}

			&:last-child {
				margin-right: 0;
			}

			input {
				display: none;

				&:checked ~ .check {
					&::before {
						left: auto;
						right: 0;
						background-color: var(--c-main);
					}
				}
			}

			.check {
				position: relative;
				margin-right: rem(5);
				width: rem(28);
				height: rem(9);
				border-radius: rem(60);
				background-color: #E3E2E3;

				&::before {
					transition: all 200ms ease;
					content: "";
					position: absolute;
					top: rem(-4);
					left: 0;
					width: rem(16);
					height: rem(16);
					border-radius: 50%;
					background-color: #CDC1C1;
					box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, .01);
				}
			}
		}
	}
}